'use client';

import Link from 'next/link';

export default function HelpPage() {
    const helpOptions = [
        {
            id: '1',
            title: 'FAQs',
            description: 'Find answers to common questions',
            icon: '‚ùì'
        },
        {
            id: '2',
            title: 'Contact Support',
            description: 'Get help from our support team',
            icon: 'üí¨'
        },
        {
            id: '3',
            title: 'Report a Problem',
            description: 'Let us know about any issues',
            icon: '‚ö†Ô∏è'
        }
    ];

    return (
        <div className="min-h-screen bg-black text-white flex flex-col">
            {/* Status Bar */}
            <div className="flex justify-between items-center p-4 pt-12 text-sm">
                <span>9:41</span>
                <div className="flex items-center gap-1">
                    <div className="flex gap-1">
                        <div className="w-1 h-1 bg-white rounded-full"></div>
                        <div className="w-1 h-1 bg-white rounded-full"></div>
                        <div className="w-1 h-1 bg-white rounded-full"></div>
                        <div className="w-1 h-1 bg-white rounded-full"></div>
                    </div>
                    <svg className="w-4 h-4" viewBox="0 0 24 24" fill="white">
                        <path d="M2 17h20v2H2v-2zm1.15-6.05L4 9.47l.85 1.48H5c.55 0 1 .45 1 1s-.45 1-1 1-.45-.45-.45-1zm5.5 2.5H9c.55 0 1-.45 1-1s-.45-1-1-1-.45.45-.45 1z" />
                    </svg>
                    <div className="w-6 h-3 border border-white rounded-sm">
                        <div className="w-4 h-1.5 bg-white rounded-sm m-0.5"></div>
                    </div>
                    <svg className="w-4 h-4" viewBox="0 0 24 24" fill="white">
                        <path d="M12 2C13.1 2 14 2.9 14 4C14 5.1 13.1 6 12 6C10.9 6 10 5.1 10 4C10 2.9 10.9 2 12 2Z" />
                    </svg>
                </div>
            </div>

            {/* Header */}
            <div className="flex flex-col items-center px-8 mb-12 text-center">
                <div className="w-16 h-16 bg-gray-800 rounded-full flex items-center justify-center mb-6">
                    <svg className="w-10 h-10 text-white" viewBox="0 0 24 24" fill="currentColor">
                        <path d="M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm-2 15l-5-5 1.41-1.41L10 14.17l7.59-7.59L19 8l-9 9z" />
                    </svg>
                </div>

                <h1 className="text-3xl font-bold">Help Center</h1>
            </div>

            {/* Help Options */}
            <div className="flex-1 px-8 space-y-4">
                {helpOptions.map((option) => (
                    <button
                        key={option.id}
                        className="w-full bg-gray-900 rounded-lg p-6 text-left hover:bg-gray-800 transition-colors"
                    >
                        <div className="flex items-center justify-between">
                            <div className="flex items-center space-x-4">
                                <div className="w-12 h-12 bg-gray-700 rounded-lg flex items-center justify-center">
                                    <span className="text-2xl">{option.icon}</span>
                                </div>
                                <div>
                                    <h3 className="text-white text-lg font-semibold">{option.title}</h3>
                                    <p className="text-gray-400 text-sm">{option.description}</p>
                                </div>
                            </div>
                            <svg className="w-6 h-6 text-gray-400" viewBox="0 0 24 24" fill="currentColor">
                                <path d="M9 6l6 6-6 6" />
                            </svg>
                        </div>
                    </button>
                ))}
            </div>

            {/* Back Button */}
            <div className="p-8">
                <Link
                    href="/profile"
                    className="w-full bg-gray-800 text-white py-4 rounded-full text-lg font-medium text-center block hover:bg-gray-700 transition-colors"
                >
                    Back to Profile
                </Link>
            </div>

            {/* Bottom Indicator */}
            <div className="flex justify-center pb-6">
                <div className="w-32 h-1 bg-white rounded-full"></div>
            </div>
        </div>
    );
} 